import React, { useState } from 'react';

const backendUrl = process.env.GATSBY_BE_URL;

const feedbackMutation = `mutation feedback($input: String!) {
  feedback(input: $input)
}`;

class FeedbackForm extends React.Component {
  state = {
    details: '',
  };

  handleInputChange = (event) => {
    const { target } = event;
    const { value } = target;
    const { name } = target;

    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();

    fetch(backendUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json',
      },
      body: JSON.stringify({
        query: feedbackMutation,
        variables: { input: this.state.details },
      }),
    });
  };

  render() {
    const { state } = this;
    const { details } = state.details;
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          <input
            type="text"
            name="details"
            value={details}
            onChange={this.handleInputChange}
          />
        </label>
        <button type="submit">Submit</button>
      </form>
    );
  }
}

export default FeedbackForm;
